.banner-section{
    background-color: #FFC400;
    margin-bottom: 30px;
    padding-bottom: 0px;
}
.banner-right-img img{
    width: 100%;
    margin-top: 30px;
}
.banner-left-item{
    padding-top:20%;
    margin-bottom: 20px;
}
.banner-left-item h1{
    color: white;
    word-spacing: 1;
    font-size: 38px;
    font-weight: 600;
    margin-bottom: 15px;
    font-family: 'Roboto', sans-serif;
}
.banner-left-item p{
    color: white;
    line-height: 27px;
    font-size: 18px;
    font-weight: 300;
    font-family: 'Roboto', sans-serif;
}
 .banner-button{
    padding: 10px !important;
    font-size: 15px;
    font-weight: 400;
    background-color: #00C0CC !important;
    border : 1px solid #00C0CC !important;
    border-radius: 6px;
    color: white;
    font-family: 'Roboto', sans-serif;
}
.banner-button:last-child{
    margin-left: 10px !important;
    background-color: initial !important;
    border : 1px solid white !important;
}
.banner-button:hover{
    background-color: #00C0CC !important;
    border : 1px solid #00C0CC !important;
}
@media(max-width:900px) {
    .banner-left-item {
        padding-top: 13% !important;
    }
    .banner-section {
        padding-bottom: 35px !important;
    }
}
@media(max-width:567px) {
    .banner-left-item h1 {
        font-size: 34px !important;
    }
    .banner-section{
        margin-bottom: 10px !important;
        padding-bottom: 35px !important;
    }
}

