.about-section{
    margin-bottom: 30px;
}
.about-right-item img{
    width: 100%;
}
.about-left-item{
    margin-top: 22%;
}
.about-left-item h3{
    color: #000000ad;
    word-spacing: 1;
    font-size: 38px;
    font-weight: 600;
    margin-bottom: 15px;
    font-family: 'Roboto', sans-serif;
}
.about-left-item p{
    color: #000000ad;
    line-height: 27px;
    font-size: 18px;
    font-weight: 300;
    margin-bottom: 20px;
    font-family: 'Roboto', sans-serif;
}
.about-button{
    padding: 10px;
    font-size: 15px;
    font-weight: 400;
    background-color: #FFC400;
    border : 1px solid #FFC400;
    border-radius: 6px;
    color: white;
    font-family: 'Roboto', sans-serif;
}
@media(max-width:1024px) {
    .about-left-item {
        margin-top: 15% !important;
    }
}
@media(max-width:900px) {
    .about-left-item {
        margin-top: 7% !important;
    }
}
@media(max-width:567px) {
    .about-left-item h3 {
        font-size: 34px !important;
    }
}