.title-section{
    background-color: #FFC400;
    margin-bottom: 30px;
}
.heading-title h3{
    color: white;
    word-spacing: 1;
    font-size: 38px;
    font-weight: 600;
    margin-bottom: 15px;
    font-family: 'Roboto', sans-serif;
}
.heading-title p{
    color: white;
    line-height: 27px;
    font-size: 24px;
    font-weight: 400;
    margin-bottom: 20px;
    font-family: 'Roboto', sans-serif;
}
.title-item-container{
    padding: 20px;
}
.title-item-container img{
    width: 100%;
    border: 4px solid white;
}
.item-title h5{
    color: white;
    word-spacing: 1;
    font-size: 24px;
    font-weight: 600;
    margin-top: 20px;
    font-family: 'Roboto', sans-serif;
}
.item-title p{
    color: white;
    line-height: 25px;
    font-size: 18px;
    font-weight: 300;
    margin-bottom: 20px;
    font-family: 'Roboto', sans-serif;
}
@media(max-width:567px) {
    .heading-title h3 {
        font-size: 34px !important;
    }
}