.resentpost-section{
    margin-bottom: 40px;
}
.post-item-container{
    border: 1px solid #E2E2E2;
    background-color: white;
}
.post-item-container:hover{
    border: 1ps solid white;
    transition: 0.6s;
    cursor: pointer;
    box-shadow: 0 10px 20px rgba(0,0,0,0.19), 0 6px 6px rgba(0,0,0,0.23);
    /* transform: translate(1.144); */   
}
.post-title{
    padding: 0 20px 0;
}
.post-title-top h3{
    color: #000000ad;
    word-spacing: 1;
    font-size: 38px;
    font-weight: 600;
    margin-bottom: 10px;
    font-family: 'Roboto', sans-serif;
}
.post-title-top p{
    color: #000000ad;
    line-height: 27px;
    font-size: 24px;
    font-weight: 400;
    margin-bottom: 20px;
    font-family: 'Roboto', sans-serif;
}
.post-item-container img{
    width: 100%;
    height: 320px;
}
.post-title h5{
    color: #000000ad;
    word-spacing: 1;
    font-size: 22px;
    font-weight: 600;
    margin-top: 20px;
    font-family: 'Roboto', sans-serif;
}
.post-title strong{
    color: #00C0CC;
    line-height: 25px;
    font-size: 14px;
    font-weight: 500;
    margin-bottom: 0px;
    font-family: 'Roboto', sans-serif;
}
.post-title p{
    color: grey;
    line-height: 25px;
    font-size: 17px;
    font-weight: 300;
    margin:10px 0 10px;
    font-family: 'Roboto', sans-serif;
}
.post-title h4{
    color: #00C0CC;
    line-height: 25px;
    font-size: 17px;
    font-weight: 600;
    margin-bottom: 20px;
    font-family: 'Roboto', sans-serif;
}
.post-title > h4:hover{
    color: #FFC400;  
}
@media(max-width:567px) {
    .post-title-top h3 {
        font-size: 34px !important;
        margin-bottom: 24px !important;
    }
  }
