.team-section{
    margin-bottom: 30px;
}
.top-team-title h3{
    color: #000000ad;
    word-spacing: 1;
    font-size: 38px;
    font-weight: 600;
    margin-bottom: 15px;
    font-family: 'Roboto', sans-serif;
}
.top-team-title p{
    color: #000000ad;
    line-height: 27px;
    font-size: 24px;
    font-weight: 400;
    margin-bottom: 20px;
    font-family: 'Roboto', sans-serif;
}
.team-item-container{
    padding: 20px;
}
.team-item-container img{
    width: 100%;
    border: 4px solid white;
}
.team-title h5{
    color: #000000ad;
    word-spacing: 1;
    font-size: 24px;
    font-weight: 600;
    margin-top: 20px;
    font-family: 'Roboto', sans-serif;
}
.team-title p{
    color: #000000ad;
    line-height: 25px;
    font-size: 18px;
    font-weight: 300;
    margin-bottom: 10px;
    font-family: 'Roboto', sans-serif;
}
.fa-brands{
    padding: 5px;
    background: #FFC400;
    border-radius: 20px;
    color: white;
    margin-right: 8px;
}
.fa-brands:hover{
    background: #00C0CC;
    cursor: pointer;
}
@media(max-width:567px) {
    .top-team-title h3 {
        font-size: 34px !important;
    }
}