.navbar-section{
    background-color: #e8ae00;
}
.navbar-custom{
    background: rgb(232, 174, 0);
    box-shadow: 0 0px 2px rgb(232 174 0), 0 1px 2px rgb(232 174 0);
}
.logo img{
    height: 55px;
    width: 100%;
}
.nav-item > a{
    color: white !important;
    font-size: 25px;
    font-weight: 600;
    font-family: 'Open Sans', sans-serif !important;
}
input{
    border: none;
    border-radius: 0px;
    background-color:#FFC400;
    font-size: 20px;
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
}
.form-control:focus {
    border-color: #fff;
    -webkit-box-shadow: none;
    box-shadow: none;
  }
.btn{
    border-color: #fff;
    -webkit-box-shadow: none;
    box-shadow: none;
}
.navbar-toggler{
    outline: none !important;
    border-radius: 0px !important;
    background-color: inherit !important;
    color: white !important;
    margin: 0px !important;
    border: none !important;
}