
.clients {
    width: 100%;
    height: 310px;
    position: relative;
    color: white;
    text-align: center;
  }
  .happy-clients{
    margin-top: 25%;
  }
  .client-icon{
    margin-bottom: 18px;
    font-size: 28px;
  }
  .clients img {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    z-index:99;
  }
  .clients .overlay {
    width: 100%;
    height: 310px;
    background-color: rgba(0, 35, 82, 0.7);
    position: absolute;
    top: 0;
    left: 0;
    z-index:999
  }
  .happy-clients > h3 {
    font-size: 35px;
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
  } 
  .happy-clients > h4 {
    font-size: 30px;
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
  }
  @media(max-width:900px) {
    .happy-clients > h3 {
      font-size: 30px !important;
  }
  }
  
  @media(max-width:567px) {
    .clients {
      height: 720px !important;
  }
  .clients .overlay {
    height: 720px !important;
  }
  .happy-clients > h3 {
    font-size: 34px !important;
}
  }
  