.footer-section{
    background-color: black;
    padding-bottom: 30px;
}
.footer-list{
    list-style-type: none;
    margin: 0;
    padding: 0;
}

.footer-item h6 ,li{
    color: white;
}
.footer-item h6{
    color: white;
    font-size: 20px;
    font-weight: 500;
    margin: 30px 0 15px;
    font-family: 'Roboto', sans-serif;
}
.footer-item ul > li{
    color: white;
    font-size: 14px;
    font-weight: 400;
    margin: 6px 0 0px;
    font-family: 'Roboto', sans-serif;
}
.footer-list li:hover{
    color: #00C0CC;
    cursor: pointer;
    border-bottom: #00C0CC;
}
.footer-item-right{
    text-align: right;
}
.footer-item-right p{
    color: white;
    font-size: 15px;
    font-weight: 300;
    margin: 0 0 25px;
    font-family: 'Roboto', sans-serif;
}
.footer-item-right h6{
    color: white;
    font-size: 20px;
    font-weight: 500;
    margin: 30px 0 10px;
    font-family: 'Roboto', sans-serif;
}
.footer-social-media .fa-brands{
    padding: 5px;
    margin-top: 20px;
    background: white;
    border-radius: 20px;
    color: black;
    margin-right: 8px;
}
.fa-brands:hover{
    background: #00C0CC;
    cursor: pointer;
}
.footer-input{
    border: none;
    border-radius: 0px;
    background-color:#FFC400;
    font-size: 20px;
    font-weight: 500;
    font-family: 'Roboto', sans-serif;
}
.footer-input:focus{
    outline: none !important;
}
input{
    border-radius: 0px !important;  
}
button{
    outline: none !important;
    border-radius: 0px !important;
    background-color: #00C0CC !important;
    color: white !important;
    margin: 0px !important;
    border: none !important;
}
@media (max-width:567px) {
    .footer-item-right {
        text-align: left !important;
    }
}

